body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

.flex {
  display: flex;
}

.center-content {
  justify-content: center;
}

.space-between {
  justify-content: space-between;
}
.space-around{
  justify-content: space-around;
}

.align-items-center {
  align-items: center;
}

.full-view {

  width: 100vw;
  height: 100vh;

}

.fixed-black {
  position: fixed;
  top: 0px;
  left: 0px;
  background-color: #1A1A1Daa;
}

.black {
  background-color: #1A1A1Daa;
}

.heading {
  color: white;
  font-size: 1.5em;
  padding-bottom: 10px;
  border-bottom: 2px solid white;
}

.dialog-box {
  width: 400px;
  height: 200px;
  background-color: #1A1A1D;
  border-radius: 50px;
  flex-direction: column;
}

.chat-container {
  width: 800px;
  height: 500px;
}

.message-container {
  height: 100%;
  width: 70%;
  background-color: #fff;
}

.room-user-container {
  height: 100%;
  width: 20%;
  background-color: #fff;
  flex-direction: column;
}

.message-window {
  height: 90%;

}

.window-container {
  height: 40%;
  flex-direction: column;
}

.mx-auto{
  margin: 0px auto;
}
.button {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #2979FF;
  padding: 5px;
  margin: 10px;
  border-radius: 5px;
  display: inline-block;
  border: none;
}

.username-input {
  padding: 5px 20px;
  width: 70%;
}

.mt-20 {
  margin-top: 20px;
}

.room-item {
  margin-bottom: 20px;
  padding: 5px 20px;
  text-align: center;
  width: 80%;
  background-color: aliceblue;
  border-radius: 5px;
}

.user-item {
  margin-bottom: 20px;
  padding: 5px 20px;
  text-align: center;
  width: 80%;
  background-color: aliceblue;
  border-radius: 5px;
}

.window-header {
  text-align: center;
  width:80%;
  margin-bottom: 20px;
}

.room-item:hover {
  cursor: pointer;
  background:  #2979FF !important;
}

.form {
  display: flex;
  border-top: 2px solid #D3D3D3;
  height: 10%;
}

.input {
  border: none;
  border-radius: 0;
  padding: 5px 5%;
  width: 80%;
  font-size: 1.2rem;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

.sendButton {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #2979FF;
  display: inline-block;
  border: none;
  width: 20%;
}